e.name }}\n              </option>\n            </select>\n          </div>\n\n          <!-- ScaleId Dropdown -->\n          <div class=\"field\">\n            <label for=\"ScaleId\">irketin Bykl</label>\n            <select\n                id=\"ScaleId\"\n                v-model=\"formData.scaleId\"\n                :disabled=\"!companyScales.length\"\n            >\n              <option value=\"\">--Seiniz--</option>\n              <option v-for=\"scale in companyScales\" :key=\"scale.id\" :value=\"scale.id\">\n                {{ scale.name }}\n              </option>\n            </select>\n          </div>\n\n          <!-- WorklineId Dropdown -->\n          <div class=\"field\">\n            <label for=\"WorklineId\">irketin  Kolu</label>\n            <select\n                id=\"WorklineId\"\n                v-model=\"formData.worklineId\"\n                :disabled=\"!worklines.length\"\n            >\n              <option value=\"\">--Seiniz--</option>\n              <option v-for=\"workline in worklines\" :key=\"workline.id\" :value=\"workline.id\">\n                {{ workline.name }}\n              </option>\n            </select>\n          </div>\n        </div>\n\n        <button\n            id=\"btnAddCompany\"\n            class=\"ui button positive\"\n            @click.prevent=\"addCompany\"\n        >\n          irket Ekle\n        </button>\n      </form>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  name: \"CompanyModal\",\n  props: {\n    isMain: {\n      type: Boolean,\n      default: false\n    },\n    // Data passed from the parent, e.g., API dropdown options\n    companyTypes: {\n      type: Array,\n      default: () => [],\n    },\n    companyScales: {\n      type: Array,\n      default: () => [],\n    },\n    worklines: {\n      type: Array,\n      default: () => [],\n    },\n    companies: {\n      type: Array,\n      default: () => [],\n    },\n  },\n  data() {\n    return {\n      formData: {\n        companyName: \"\",\n        typeId: \"\",\n        scaleId: \"\",\n        worklineId: \"\",\n        mainCompanyId: \"\",\n        isMain: this.isMain,\n      },\n    };\n  },\n  methods: {\n    openModal() {\n      console.log(this.formData);\n      $(this.$refs.modal).modal(\"show\");\n    },\n    closeModal() {\n      $(this.$refs.modal).modal(\"hide\");\n    },\n    addMainCompany() {\n      console.log(\"Adding Main Company:\", this.formData);\n      this.closeModal();\n    },\n    addCompany() {\n      console.log(\"Adding Company:\", this.formData);\n      const company = {\n        name: this.formData.companyName,\n        typeId: this.formData.typeId,\n        scaleId: this.formData.scaleId,\n        worklineId: this.formData.worklineId,\n        isMain: this.formData.isMain\n      }\n      console.log(\"Adding Company:\", company);\n      this.$emit(\"save-company\", company);\n      this.closeModal();\n    }\n  },\n};\n</script>\n\n<style scoped>\n/* Add any scoped styles for the modal if needed */\n</style>\n"],"sourceRoot":""}]);
