`, "",{"version":3,"sources":["webpack://./src/components/news/ListNews.vue"],"names":[],"mappings":";AA+JA;EACE,aAAa;EACb,gCAAgC;AAClC;AACA;EACE,gBAAgB;EAChB,aAAa;EACb,mBAAmB;AACrB;AACA;EACE,kBAAkB;AACpB","sourcesContent":["<template>\n    <!-- Header -->\n    <h4 class=\"ui dividing header\">Haberler</h4>\n\n    <!-- Filter Section -->\n    <div class=\"ui attached secondary segment\">\n      <div class=\"ui small filter form\">\n        <div class=\"fields\">\n          <!-- Year Dropdown -->\n          <div class=\"four wide field\">\n            <select\n                v-model=\"selectedYear\"\n                class=\"ui fluid search selection dropdown\"\n            >\n              <option v-for=\"year in years\" :key=\"year\" :value=\"year\">\n                {{ year }}\n              </option>\n            </select>\n          </div>\n\n          <!-- Month Dropdown -->\n          <div class=\"five wide field\">\n            <select\n                  v-model=\"selectedMonth\"\n                class=\"ui fluid search selection dropdown\"\n            >\n              <option value=\"\">--Seiniz--</option>\n              <option v-for=\"(month, index) in months\" :key=\"index\" :value=\"index + 1\">\n                {{ month }}\n              </option>\n            </select>\n          </div>\n\n          <!-- Filter Button -->\n          <div class=\"four wide field\">\n            <button @click=\"fetchNews\" class=\"ui left labeled icon button\">\n              <i class=\"filter icon\"></i>Filtrele\n            </button>\n          </div>\n        </div>\n      </div>\n    </div>\n\n    <!-- News Section -->\n    <div style=\"overflow-y: scroll; height:700px;\">\n    <div v-for=\"item in filteredNews\" :key=\"item.id\" class=\"item\" :data-id=\"item.id\">\n      <div class=\"content\">\n        <!-- Header and Link -->\n        <a\n            v-if=\"item.added\"\n            href=\"#\"\n            :data-id=\"item.id\"\n            data-open=\"0\"\n            class=\"a\"\n            style=\"color:red\"\n        >\n          {{ item.header }}\n        </a>\n        <a\n            v-else\n            href=\"#\"\n            :data-id=\"item.id\"\n            data-open=\"0\"\n            class=\"a\"\n        >\n          {{ item.header }}\n        </a>\n\n        <!-- External Link -->\n        <a :href=\"item.link\" target=\"_blank\">\n          <i\n              class=\"arrow alternate circle right outline icon\"\n              style=\"color: red\"\n              v-if=\"item.added\"\n          ></i>\n          <i\n              class=\"arrow alternate circle right outline icon\"\n              v-else\n          ></i>\n        </a>\n\n        <!-- Meta Section (Date) -->\n        <div class=\"meta\">\n          <span class=\"date\">{{ formatDate(item.date) }}</span>\n          <button\n              @click=\"copyLink(item.id)\"\n              class=\"ui icon button green basic mini copyLink\"\n              :data-id=\"item.id\"\n          >\n            <i class=\"paperclip icon\"></i>\n          </button>\n        </div>\n\n        <!-- Description (Hidden by Default) -->\n        <div\n            class=\"description\"\n            :data-id=\"item.id\"\n            v-show=\"false\"\n        ></div>\n      </div>\n    </div>\n    \n    </div>\n  \n</template>\n\n<script>\nimport {fetchWithToken} from \"../../fetchWrapper\";\n\nexport default {\n  name: \"ListNews\",\n  data() {\n    return {\n      selectedYear: new Date().getFullYear(), // Default to current year\n      selectedMonth: \"\", // No month selected by default\n      years: Array.from({ length: new Date().getFullYear() - 2018 + 1 }, (_, i) => 2019 + i), // Generate years dynamically\n      months: [\n        \"Ocak\",\n        \"ubat\",\n        \"Mart\",\n        \"Nisan\",\n        \"Mays\",\n        \"Haziran\",\n        \"Temmuz\",\n        \"Austos\",\n        \"Eyll\",\n        \"Ekim\",\n        \"Kasm\",\n        \"Aralk\",\n      ],\n      news: [], // Array to hold news items\n    };\n  },\n  computed: {\n    filteredNews() {\n      // Filter news based on selected year and month\n      return this.news;\n    },\n  },\n  mounted() {\n    // Fetch news data when the component is mounted\n    this.fetchNews();\n  },\n  methods: {\n    fetchNews() {\n      console.log(this.selectedYear);\n      // Simulate an API call to fetch news data\n      fetchWithToken(`/resistance/news?year=${this.selectedYear}&month=${this.selectedMonth}`)\n          .then(response => response.json())\n          .then(data => (this.news = data));\n    },\n    formatDate(date) {\n      return new Date(date).toLocaleDateString(); // Format date to a readable string\n    },\n  },\n};\n</script>\n\n<style scoped>\n.item {\n  padding: 1rem;\n  border-bottom: 1px solid #f1f1f1;\n}\n.meta {\n  margin-top: 10px;\n  display: flex;\n  align-items: center;\n}\n.date {\n  margin-right: 10px;\n}\n</style>\n"],"sourceRoot":""}]);
