    <div class="field">
      <label for="ProtestoTypeIds">Eylem Trleri</label>

      <multiselect
          id="ProtestoTypeIds"
          v-model="protesto.protestoTypeIds"
          :options="protestoTypeOptions"
          :multiple="true"
          :close-on-select="false"
          :clear-on-select="false"
          :preserve-search="true"
          placeholder="Seiniz"
          label="name"
          track-by="id"
      ></multiselect>
    </div>

    <!-- Start and End Dates -->
    <div class="two fields">
      <div class="field">
        <label for="ProtestoStartDate">Balang Tarihi</label>
        <VueDatePicker v-model="protesto.protestoStartDate"></VueDatePicker>
      </div>
      <div class="field">
        <label for="ProtestoEndDate">Biti Tarihi</label>
        <VueDatePicker v-model="protesto.protestoEndDate"></VueDatePicker>
      </div>
    </div>

    <!-- Protesto Places -->
    <div class="field">
      <label for="ProtestoPlaceIds">Yerler</label>
      <multiselect
          id="ProtestoPlaceIds"
          v-model="protesto.protestoPlaceIds"
          :options="protestoPlaceOptions"
          :multiple="true"
          :close-on-select="false"
          :clear-on-select="false"
          :preserve-search="true"
          placeholder="Seiniz"
          label="name"
          track-by="id"
      ></multiselect>
    </div>

    <!-- Gender -->
    <div class="field">
      <label for="GenderId">Cinsiyet</label>
      <select v-model="protesto.genderId" id="GenderId">
        <option value="">--Seiniz--</option>
        <option
            v-for="gender in genderOptions"
            :key="gender.id"
            :value="gender.id"
        >
          {{ gender.name }}
        </option>
      </select>
    </div>

  <div class="field">
    <label for="CustodyCount">Gzalt Says</label>
    <input
        type="number"
        id="CustodyCount"
        v-model="protesto.custodyCount"
    />
  </div>
    <!-- Other Fields -->
    <div class="two fields">
      <div class="field">
        <label for="EmployeeCountInProtesto">Eylemdeki i Says (Tam)</label>
        <input
            type="number"
            id="EmployeeCountInProtesto"
            v-model="protesto.employeeCountInProtesto"
        />
      </div>
      <div class="field">
        <label for="EmployeeCountInProtestoId">Eylemdeki i Says</label>
        <select v-model="protesto.employeeCountInProtestoId" id="EmployeeCountInProtestoId">
          <option value="">--Seiniz--</option>
          <option
              v-for="employeeCountInProtesto in employeeCountInProtestoOptions"
              :key="employeeCountInProtesto.id"
              :value="employeeCountInProtesto.id"
          >
            {{ employeeCountInProtesto.name }}
          </option>
        </select>
      </div>
    </div>

    <!-- Notes -->
    <div class="field">
      <label for="Note">Notlar</label>
      <textarea id="Note" v-model="protesto.note" rows="3"></textarea>
    </div>

    <!-- Save Button -->
   
</template>

<script>
import Multiselect from "vue-multiselect";
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'
import {fetchWithToken} from "../../fetchWrapper";

export default {
  name: "Protesto",
  components: { Multiselect, VueDatePicker },
  data() {
    return {
      protestoPlaceOptions: [],
      protestoTypeOptions: [],
      genderOptions: [],
      employeeCountInProtestoOptions:[],
    };
  },
  props: {
    protesto: {
      type: Object,
      default: () => ({})  // Prevents undefined errors
    },
  },
  mounted() {
    fetchWithToken("/lookup/employeeCounts")
        .then(response => response.json())
        .then(data => (this.employeeCounts = data));

    fetchWithToken("/ProtestoPlace/List")
        .then(response => response.json())
        .then(data => (this.protestoPlaceOptions = data));
    fetchWithToken("/ProtestoType/List")
        .then(response => response.json())
        .then(data => (this.protestoTypeOptions = data));

    fetchWithToken("/lookup/genders")
        .then(response => response.json())
        .then(data => (this.genderOptions = data));

  }
};
</script>

